@import url(https://fonts.googleapis.com/css2?family=Brawler&display=swap);
/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  font-size: 0px;
  line-height: 0;
}
html,
body,
#root,
.App {
  height: 100%;
}
.App {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background: url(/find-my-ip/static/media/world_map.f5916778.jpg);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
h1,
h2 {
  font-family: "Brawler", serif;
  text-align: center;
  max-width: 80%;
}
h1 {
  font-size: 48px;
  margin-bottom: 16px;
}
h2 {
  font-size: 36px;
  text-decoration: underline;
  color: rgba(206, 24, 24, 0.452);
}
span {
  color: rgba(0, 136, 255, 0.829);
}

